<template>
    <div class='user'>
        <div class="userFlex">
            <div class="left">
                <div @click="$router.push({path:'/ziliao',query:{title:$t('user[0]')}});ageActive = 1" :class="{checked:ageActive == 1}">
                    {{$t('user[0]')}}
                </div>
                <div @click="$router.push({path:'/bianjiziliao',query:{title:$t('user[1]')}});ageActive = 2" :class="{checked:ageActive == 2}">
                    {{$t('user[1]')}}
                </div>
                <div @click="$router.push({path:'/dizhiguanli',query:{title:$t('user[2]')}});ageActive = 3" :class="{checked:ageActive == 3}">
                    {{$t('user[2]')}}
                </div>
                <div @click="$router.push({path:'/gouwuche',query:{title:$t('user[3]')}});ageActive = 8" :class="{checked:ageActive == 8}">
                    {{$t('user[3]')}}
                </div>
                <div @click="$router.push({path:'/wodedingdan',query:{title:$t('user[4]')}});ageActive = 4" :class="{checked:ageActive == 4}">
                    {{$t('user[4]')}}
                </div>
                <div @click="$router.push({path:'/shoucang',query:{title:$t('user[5]')}});ageActive = 5" :class="{checked:ageActive == 5}">
                    {{$t('user[5]')}}
                </div>
                <div @click="$router.push({path:'/xiugaimima',query:{title:$t('user[6]')}});ageActive = 6" :class="{checked:ageActive == 6}">
                    {{$t('user[6]')}}
                </div>
                <div @click="$router.push({path:'/zhaohuo',query:{title:$t('user[7]')}});ageActive = 7" :class="{checked:ageActive == 7}">
                    {{$t('user[7]')}}
                </div>
            </div>
            <div class="right">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            ageActive:1,
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        if(_this.$route.name == 'gouwuche'){
            _this.ageActive = 8
        }
        if(_this.$route.name == 'wodedingdan'){
            _this.ageActive = 4
        }
    },
    watch:{
    },
    methods:{
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.user{
    .userFlex{
        width: 1200px;
        margin: 0 auto;
        margin-top: 25px;
        display: flex;
        align-items: top;
        .left{
            width: 140px;
            text-align: center;
            color: #383838;
            font-size: 14px;
            font-weight: bold;
            line-height: 28px;
            &>div{
                cursor:pointer;
                margin: 5.5px 0;
            }
            .checked{
                color: #FF7800;
                font-size: 16px;
            }
        }
        .right{
            flex: 1;
            margin-top: 10px;
        }
    }
}
</style>


